import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import createMutationsSharer from 'vuex-shared-mutations';

const store = {};
Vue.use(Vuex);

store.state = () => ({
  minisLang: 'ru',
  minisTheme: 'dark',
  minisCalculatorHistory: [],
});


store.getters = {
  lang: state => state.minisLang,
  theme: state => state.minisTheme,
  history: state => state.minisCalculatorHistory,
};


store.mutations = {
  addToHistory(state, data) {
    const newHistory = [...state.minisCalculatorHistory.slice(-9), data];
    Vue.set(state, 'minisCalculatorHistory', newHistory);
  },
  clearHistory(state) {
    Vue.set(state, 'minisCalculatorHistory', []);
  },
  switchTheme(state) {
    const newTheme = state.minisTheme == 'light' ? 'dark' : 'light';
    Vue.set(state, 'minisTheme', newTheme);
  },
};


const pluginsState = ['minisTheme', 'minisLang', 'minisCalculatorHistory'];
store.plugins = [
  createMutationsSharer({ predicate: () => pluginsState }),
  createPersistedState({ paths: pluginsState }),
];

export default new Vuex.Store(store);
